import React from 'react'
import {
	Switch,
	useLocation,
	Route
} from "react-router-dom"
import {
	AnimatePresence
} from "framer-motion"

import Home from "./pages/Home"
import About from "./pages/About"

// Articles
import Articles from "./pages/Content/Articles/LaunchArticles"
import SimpleCNN from "./pages/Content/Articles/ArticleFiles/SimpleCNN"
import RLTaxi from './pages/Content/Articles/ArticleFiles/RLTaxi'

// Projects
import Projects from "./pages/Content/Projects/LaunchProjects"
import SuguruSolver from './pages/Content/Projects/ProjectFiles/SuguruSolver'
import ComputerBuild from "./pages/Content/Projects/ProjectFiles/ComputerBuild"
import TetrisAI from "./pages/Content/Projects/ProjectFiles/TetrisAI"
import NumberRecognizer from "./pages/Content/Projects/ProjectFiles/NumberRecognizer"

const App = () => {
	const location = useLocation()

	return (
		<AnimatePresence initial={false} exitBeforeEnter>
			<Switch location={location} key={location.pathname}>
				<Route path="/website/" exact component={Home} />
				<Route path="/website/about" exact component={About} />

				<Route path="/website/articles" exact component={Articles} />
				<Route path="/website/articles/simple-cnn" exact component={SimpleCNN} />
				<Route path="/website/articles/rl-taxi" exact component={RLTaxi} />

				<Route path="/website/projects" exact component={Projects} />
				<Route path="/website/projects/suguru-solver" exact component={SuguruSolver} />
				<Route path="/website/projects/computer-build" exact component={ComputerBuild} />
				<Route path="/website/projects/tetris-ai" exact component={TetrisAI} />
				<Route path="/website/projects/number-recognizer" exact component={NumberRecognizer} />
			</Switch>
		</AnimatePresence>
	)
}

export default App
